.heart-circle {
  width: 50px;
  height: 50px;
  background-color: #696969;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 20px;
  right: 20px;
}

.heart-circle:hover {
  cursor: pointer;
  background-color: none;
}

.heart {
  width: 70%;
}

* {
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  background-color: #0c111b;
  font-size: 18px;
}

.color-text {
  color: #9ca3af;
  padding: .5%;
  font-size: 16px;
}

.content {
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 2%;
  display: flex;
}

.illustration {
  position: relative;
}

.illustration__img {
  width: 600px;
  border-radius: 15px;
}

.about-film {
  width: 60%;
}

.about-film__title {
  text-align: center;
  font-size: 100px;
}

.actors-block {
  min-height: 350px;
  flex-wrap: wrap;
  justify-content: space-between;
